<template>
	<div class="orderPage" id="app">
		<!--地址头部-->
		<section class="mod-address">
            <a href="" class="flex_between mod-cell">
            	<i class="iconfont icon-map"></i>
                <div class="mod-bd">
                    <h4 class="flex_between"> <span class="name fn-left">收货人: 廖立伟</span> <span class="tel fn-right">13251026170</span> </h4>
                    <p class="address">收货地址：浙江省杭州市西湖区三墩镇振华路86号西港新界东区x憧8楼110室</p>
                </div>
                <i class="iconfont icon-more"></i>
            </a>
        </section>
        <!--中间订单详情部分-->
        <section class="order-list">
        	<h3>选择的回报</h3>
        	<div class="order-info flex_star">
                <div class="img-box"> <img src="../../assets/images/img-repay.jpg" alt="" /> </div>
                <div class="info">
                    <p class="title">【首发礼】-超薄音响1台</p>
                    <p class="current flex_between"> <span class="price">￥100</span> <span class="count">x1</span> </p>
                </div>
            </div>
            <!--购买数量-->
            <div class="flex_between order-item">
            	<label>购买数量</label>
            	<wv-number-spinner :min="1" :max="5" v-model="productNumber" @change="onChange"></wv-number-spinner>
            </div>
            <div class="order-item">
                <div class="flex_between active_color"> <label>阶段1：首笔款</label>
                    <div class="item-main">
                        <p class="tn-r">￥20.00</p>
                    </div>
                </div>
                <div class="flex_between"> <label>阶段2：尾款</label>
                    <div class="item-main">
                        <p class="tn-r">￥80.00</p>
                    </div>
                </div>
            </div>
            <div class="order-item flex_between order-count"> <label>邮寄方式：</label>
                <div class="item-main"> <input type="text" placeholder="不限港澳台，全国包邮" /> </div>
            </div>
            <div class="order-item flex_between order-count"> <label>买家留言：</label>
                <div class="item-main"> <textarea placeholder="选填"></textarea> </div>
            </div>
        </section>
        <!--底部付款-->
        <footer class="mod-btn"> 
	        <button class="btn flex-1">
	        	合计：<span class="color-r">￥100.00</span>
	        </button> 
	        <button class="btn col-3 btn-r" @click="submitOrder">提交订单</button> 
        </footer>
	</div>
</template>

<script>
	import Lib from '../../assets/js/Lib';
	import { NumberSpinner } from 'we-vue';
	import {mapGetters, mapMutations, mapActions} from 'vuex';
	export default({
		data(){
			return{
				productNumber : 1	,	//商品件数
			}
		},
		components:{ NumberSpinner },
		created(){
			console.log(this.$store)
		},
		methods:{
			/*...mapMutations([
				'SET_NUM'
			]),*/
			onChange(){
				/*this.$store.state.num = this.productNumber
				console.log(this.$store.state.num)
				this.$store.commit('SET_NUM')*/
			},
			//提交订单
			submitOrder(){
//				this.$router.push({path:"orderDetail"})
				location.href="../orderDetail.html"
			}
		}
	})
</script>

<style lang="less">
	.orderPage{
		position: absolute;
    	left: 0;
    	top: 0;
    	width: 100%;
    	height: 100%;
		background: #f5f5f5;
	}
	/*头部地址*/
	.mod-address{
		margin-bottom: 15 / 50rem;
		.mod-cell{
			height: auto;
			min-height: 80 / 50rem;
			padding: 10 / 50rem 15 / 50rem;
			font-size: 14 / 50rem;
			background:#fff;
			.mod-bd{
				padding: 0 15 / 50rem;
				h4{
					font-size:16 / 50rem ;
					color: #202020;
				}
				p{
					font-size: 12 / 50rem;
					color: #404040;
				}
			}
		}
	}
	/*中间订单详情部分*/
	.order-list{
		background: #fff;
		h3{
			padding: 0 15 / 50rem;
    		line-height: .86rem;
    		font-size: 14 / 50rem;
    		font-weight: normal;
    		color: #202020;
		}
		.order-info{
			background: #F9F9F9;
			padding: 10 / 50rem 15 / 50rem;
			.img-box{
				height: 75 / 50rem;
				width: 75 / 50rem;
				img{
					height: 100%;
					width: 100%;
				}
			}
			.info{
				position: relative;
				padding: 0 0 0 .2rem;
				height: 75 / 50rem;
				flex: 1;
				.title{
					font-size: .24rem;
    				color: #202020;
				    width: 5.2rem;
				    line-height: .42rem;
				    overflow: hidden;
				    -o-text-overflow: ellipsis;
				    text-overflow: ellipsis;
				    white-space: normal;
				    display: -webkit-box;
				    -webkit-line-clamp: 2;
				    -webkit-box-orient: vertical;
				}
				.current{
					position: absolute;
					bottom: 0;
					width: calc(~"100% - 0.2rem");
					font-size: .24rem;
					.price{
						color: #E8575E;
					}
				}
			}
			
		}
		.order-item{
			padding: .25rem 0;
    		margin: 0 .3rem;
    		border-bottom: 1px solid #F5F5F5;
    		.wv-number-spinner{
    			height: 30 / 50rem;
    			.spinner-btn{
    				width: 30 / 50rem;
    			}
    		}
    		.item-main{
    			flex: 1;
    			text-align: right;
    			line-height: .48rem;
    			font-size: .24rem;
    			padding: 0 0 0 .2rem;
    			input{
    				display: block;
				    width: 100%;
				    height: .48rem;
				    line-height: normal;
				    border: 0;
				    text-align: right;
    			}
    			textarea{
    				width: 100%;
				    border: 0;
				    padding: .1rem;
				    text-align: right;
				    resize: none;
    			}
    		}
    		.active_color{
    			color: #E8575E;
    		}
		}
	}
	/*底部付款样式*/
	.mod-btn{
		display: flex;
	    width: 100%;
	    height: 1rem;
	    position: fixed;
	    bottom: 0;
	    left: 0;
	    box-shadow: 0 0px 20px 0px rgba(0, 0, 0, 0.08);
	    .btn{
	    	font-size: 0.34rem;
	    	border: 0;
	    }
	    .flex-1{
	    	flex: 1;
	    	background: #fff;
	    	.color-r{
	    		color: #E8575E;
	    	}
	    }
	    .btn-r{
	    	color: #FFF;
    		background: #E8575E;
    		width: 33.3%;
	    }
	}
</style>


